import { React, Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import GlitchClip from 'react-glitch-effect/core/Clip';

function Navbar() {
  return (
    <Fragment>
    
    <div className="container-fluid nav_bg">
        <div className="row">
            <div className="col-10 mx-auto">

                <nav className="navbar navbar-expand-lg navbar-light bg-transparent">    
                    <NavLink className="navbar-brand" to="/">Mridul Sharma</NavLink>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto">
                        <li className="nav-item active">
                            <NavLink exact activeClassName="menu_active" className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink activeClassName="menu_active" className="nav-link" to="/about">About</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink activeClassName="menu_active" className="nav-link" to="/interests">Interests</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink activeClassName="menu_active" className="nav-link" to="/contact">Contact</NavLink>
                        </li>
                        </ul>
                    </div>
                </nav>

            </div>
        </div>
    </div>
    </Fragment>
  );
}

export default Navbar;
