import { React, Fragment } from 'react';
import GlitchClip from 'react-glitch-effect/core/Clip';
import emailjs from 'emailjs-com';

function Contact() {

  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('gmail', 'template_hqpcckm', e.target, 'user_bEOmrRo3GYYWIoJAgQoDv')
      .then((result) => {
        console.log(result.text);
      }, (error) => {
        console.log(error.text);
      });
    e.target.reset();
  }

  return (
    <Fragment>
      <GlitchClip duration="10s">
        <div className="my-5">
          <h1 className="text-center" style={{ fontWeight: "500", color: "white" }}>Contact me</h1>
        </div>
      </GlitchClip>
      {/* <div className="container-fluid mb-5">
        <div className="row">
          <div className="col-10 mx-auto">
            <div className="row gy-4">
              <div className="d-flex h-100">
                <div className="m-auto">
                  <form onSubmit={sendEmail}>
                    <div className="row pt-5 mx-auto">
                      <div className="col-8 form-group max-auto">
                        <input type="text" className="form-control" placeholder="Name" name="name" />
                      </div>
                      <div className="col-8 form-group pt-2 max-auto">
                        <input type="email" className="form-control" placeholder="Email Address" name="email" />
                      </div>
                      <div className="col-8 form-group pt-2 max-auto">
                        <input type="text" className="form-control" placeholder="Suject" name="subject" />
                      </div>
                      <div className="col-8 form-group pt-2 max-auto">
                        <textarea className="form-control" id="" cols="30" rows="8" placeholder="Your message" name="message"></textarea>
                      </div>
                      <div className="col-8 pt-3 max-auto">
                        <button type="submit" className="btn btn-primary" value="Send Message"></button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div> */}

      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
            <form onSubmit={sendEmail}>
              <div className="form-group">
                <label for="inputName" style={{ color: "white" }}>Name</label>
                <input type="text" className="form-control" id="userName" placeholder="Enter name" name="name"/>
              </div>
              <div className="form-group">
                <label for="inputEmail" style={{ color: "white" }}>Email address</label>
                <input type="email" className="form-control" id="userEmail" placeholder="Enter email" name="email"/>
              </div>
              <div className="form-group">
                <label for="inputSubject" style={{ color: "white" }}>Subject</label>
                <input type="text" className="form-control" id="userSubject" placeholder="Enter subject" name="subject"/>
              </div>
              <div className="form-group">
                <label for="inputMessage" style={{ color: "white" }}>Message</label>
                <textarea className="form-control" id="userMessage" cols="30" rows="3" placeholder="Enter message" name="message"></textarea>
              </div>
              <button type="submit" className="btn btn-primary">Submit</button>
            </form>
          </div>
        </div>
      </div>

    </Fragment >
  );
}

export default Contact;
