import { React, Fragment } from 'react';
import spotify from "../src/images/Spotify.jpg";
import cluster from "../src/images/LrgCluster.jpg";
import GlitchClip from 'react-glitch-effect/core/Clip';

function Interests() {
  return (
    <Fragment>
      <div className="my-5">
        <GlitchClip duration="10s">
          <h1 className="text-center" style={{fontWeight : "500", color: "white"}}>
            Interests
          </h1>
        </GlitchClip>
        <div className="text-center" style={{width: "50%", margin: "0 auto"}}>
          <h5 className="text-center" style={{color: "white"}}>
            Doing different and fun things where you are always learning is very
            essential and is something I like to do a lot. My interests range from
            making YouTube videos to playing guitar, all the way to sketches.
          </h5>
        </div>
      </div>

      <div className="cara" style={{width: "50%", margin: "0 auto"}}>
        <div className="container-fluid mb-5">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row gy-4">
                <div id="carouselExampleIndicators" className="carousel" data-ride="carousel">
                  <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                  </ol>
                  <div className="carousel-inner">
                    <div className="carousel-item active">
                      <img className="d-block w-100" src={spotify} alt="First slide" />
                      <div className="carousel-caption d-none d-md-block">        
                        <h5 style={{fontWeight : "800",color: "#427A59"}}>Project 1</h5>
                        <p style={{fontWeight : "800",color: "#427A59"}}>Spotify Informations (In Progress)</p>
                      </div>
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src={cluster} alt="Second slide" />
                      <div className="carousel-caption d-none d-md-block">
                        <h5 style={{fontWeight : "800",color: "#D04C2E"}}>Project 2</h5>
                        <p style={{fontWeight : "800", color: "#D04C2E"}}>Car Information (Coming soon!)</p>
                      </div>
                    </div>
                  </div>
                  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>
                  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

    </Fragment>
  );
}

export default Interests;
