import { React, Fragment } from 'react';
import web from "../src/images/Mountain2.jpg";
import Card from "react-bootstrap/Card";
import { Button, Container, Row, Col, Image } from 'react-bootstrap';
import me from "../src/images/Me.jpg";
import art2 from "../src/images/Art2.jpg";
import isThisLove from "../src/images/ISTHISLOVE.jpg";
import cluster2 from "../src/images/Cluster.jpg";
import GlitchClip from 'react-glitch-effect/core/Clip';

function About() {
  return (
    <Fragment>
      <GlitchClip duration="10s">
        <div className="my-5">
          <h1 className="text-center" style={{ fontWeight: "500", color: "white" }}>About me</h1>
        </div>
      </GlitchClip>
      {/* 
      <div className="container-fluid mb-5">
        <div className="row">
          <div className="col-10 mx-auto">
            <div className="col-md-4 col-10 mx-auto"> */}

      <div className="text-center" style={{ width: "1050px", margin: "0 auto" }}>

        <div className="card" style={{ background: "rgba(255,255,255,0.8)", borderRadius: "3%" }}>
          <div className="center" style={{ textAlign: "center" }}>
            <Image src={me} className="card-img-top" alt="Mridul Sharma" roundedCircle style={{ width: "180px", height: "180px" }} />
          </div>

          <div className="card-body">
            <h5 className="card-title" style={{ textAlign: "center" }}>Mridul Sharma</h5>
            <h6 class="card-text">Hi! I'm Mridul, I am a Computer Science (Co-op) student in my fourth year at the University of Guelph who loves
            to work with with UI/UX Design's and front-end development. I like photography, music, playing my guitar, editing
                      videos/images via Photoshop, Illustrator, and Premiere Pro, and I also love to sketch.</h6>
          </div>

          <div className="row">
            <div className="col-md-4 col-10 mx-auto">
              <div className="card" style={{ background: "rgba(255,255,255,0.0)", height: "40vh" }}>
                <img src={art2} className="card-img-top" style={{ borderRadius: "5%" }} alt="Lisa drawing" />
                <div className="card-body">
                  <h5 className="card-title" style={{ textAlign: "center", color: "black" }}>My sketch</h5>
                </div>
              </div>
            </div>

            <div className="col-md-4 col-10 mx-auto">
              <div className="card" style={{ background: "rgba(255,255,255,0.0)", height: "40vh" }}>
                <img src={isThisLove} className="card-img-top" style={{ borderRadius: "5%" }} alt="Favorite sound track" />
                <div className="card-body">
                  <h5 className="card-title" style={{ textAlign: "center", color: "black" }}>Current favorite sound track</h5>
                </div>
              </div>
            </div>

            <div className="col-md-4 col-10 mx-auto">
              <div className="card" style={{ background: "rgba(255,255,255,0.0)", height: "40vh" }}>
                <img src={cluster2} className="card-img-top" style={{ borderRadius: "5%" }} alt="Favorite picture" />
                <div className="card-body">
                  <h5 className="card-title" style={{ textAlign: "center", color: "black" }}>Personal favorite photo</h5>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>



      {/* </div>
          </div>
        </div>
      </div> */}
      
    </Fragment>
  );
}

export default About;
